<script>
const axios = require("axios");
  export default {
    data() {
      return {
        ROLEID:'',
        SeeClickData: '',
        reason: '',
        formInline: {
          value1: '',
          value2: '',
          value3: ''
        },
        downLoad: {
          value: ''
        },
        dialogVisible: false,
        dialogVisible2: false,
        dialogVisible3: false,
        dialogVisible4: false,
        dialogVisible5: false,
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        }
      }
    },
    methods: {
      //打开弹窗
      openVisible(tab, event) {
        this.SeeClickData = event;
        this.dialogVisible = true
      },
      openVisible2(tab, event) {
        this.SeeClickData = event;
        this.dialogVisible2 = true
      },
      openVisible3(tab, event) {
        this.dialogVisible3 = true
      },
      openVisible4(tab) {
        this.dialogVisible4 = true
        this.bannerIndex = tab
      },
      openVisible5() {
        this.dialogVisible5 = true
      },
      SeeClick(tab, event) {
        this.SeeClickData = event;
        console.log(this.SeeClickData)
      },
      //打印
      printContent() {
        this.$print(this.$refs.print)
      },
      //多选框变化
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(this.multipleSelection)
      },
      //查询
      onSubmit() {
        console.log('submit!')
      },
      //分页
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`)
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`)
      },
      exportExcel() {
        require.ensure([], () => {
          const { export_json_to_excel } = require('../../excel/Export2Excel');
          const tHeader = ['设备编号', '审核通过', '审核失败','总数'];
          // 上面设置Excel的表格第一行的标题
          const filterVal = ['Machine', 'Completed', 'NotCompleted','Total'];
          // 上面的index、nickName、name是tableData里对象的属性
          const list = this.chartData.rows;  //把data里的tableData存到list
          const data = this.formatJson(filterVal, list);
          export_json_to_excel(tHeader, data, '体检人数统计');
        })
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
      }
    },
    mounted(){
      this.ROLEID = sessionStorage.getItem("ROLEID")
    },
    watch:{
      "formInline": {
        handler: (val, olVal) => {
          console.log(val)
          console.log(olVal)
        },
        deep:true
      }
    }
  };
</script>


